.readWrapper {
  .upperMenu-enter {
    transform: translateY(-100%);
  }

  .upperMenu-enter-active {
    transform: translateY(0);
    transition: transform 500ms;
  }

  .upperMenu-exit-active {
    transform: translateY(-100%);
    transition: transform 500ms;
  }

  .upperMenuWrapper {
    position: absolute;
    width: 100vw;
    top: 0;
    left: 0;
    z-index: 2;
  }

  .upperMenu {
    width: calc(
      100% - max(1rem, env(safe-area-inset-right)) -
        max(1rem, env(safe-area-inset-left))
    );
    background-color: var(--color-background);
    backdrop-filter: blur(5px);
    padding: 1rem;
    padding-right: max(1rem, env(safe-area-inset-right));
    padding-left: max(1rem, env(safe-area-inset-left));
    padding-top: max(1rem, env(safe-area-inset-top));
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;

    .close {
      padding: 0.25rem;
      display: flex;
      justify-content: center;
      align-items: center;
      cursor: pointer;
    }

    .pageOffset {
      display: flex;
      align-items: center;
      padding-right: 2rem;

      span {
        color: var(--color-text);
      }

      h3 {
        padding: 0;
        margin: 0;
        margin-right: 0.5rem;
      }
    }

    h2 {
      padding: 0;
      margin: 0;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
      max-width: calc(100vw - 14rem);
    }
  }

  .lowerMenu-enter {
    transform: translateY(100%);
  }

  .lowerMenu-enter-active {
    transform: translateY(0);
    transition: transform 500ms;
  }

  .lowerMenu-exit-active {
    transform: translateY(calc(100% + 3rem));
    transition: transform 500ms;
  }

  .lowerMenuWrapper {
    position: absolute;
    width: 100vw;
    bottom: 0;
    left: 0;
    z-index: 2;
  }

  .zoomControlWrapper {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    bottom: calc(6rem + env(safe-area-inset-bottom));
    z-index: 1;
  }

  .zoomControl-enter {
    opacity: 0;
  }

  .zoomControl-enter-active {
    opacity: 0.9;
    transition: opacity 500ms;
  }

  .zoomControl-enter-done {
    opacity: 0.9;
  }

  .zoomControl-exit-active {
    opacity: 0;
    transition: opacity 500ms;
  }

  .zoomControl {
    display: flex;
    gap: 1rem;
    align-items: center;
    background-color: var(--color-chapters-background);
    padding: 1rem;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    border-radius: 2rem;
    color: var(--color-chapters-text);

    h3 {
      margin: 0;
    }

    div {
      display: flex;
      justify-content: center;
      align-items: center;
    }
  }

  .lowerMenu {
    width: calc(
      100% - max(1rem, env(safe-area-inset-right)) -
        max(1rem, env(safe-area-inset-left))
    );
    background-color: var(--color-background);
    backdrop-filter: blur(5px);
    padding: 1rem;
    padding-right: max(1rem, env(safe-area-inset-right));
    padding-left: max(1rem, env(safe-area-inset-left));
    padding-bottom: max(1rem, env(safe-area-inset-bottom));
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    h3 {
      padding: 0;
      margin: 0;
    }

    .sliderWrapper {
      width: calc(100% - 2rem);
    }
  }
}
